<template>
  <div class="appointreport" >
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="" >
         <el-input v-model="formInline.time1" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formInline.name1" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="formInline.state" placeholder="">
          <el-option label="" value="ture"></el-option>
          <el-option label="" value="false"></el-option>
          <el-option label="" value="false"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="success" class="el-icon-search"@click="onSubmit"></el-button>
      </el-form-item>
      <el-button class="filter-item" type="primary" @click="handleCreate()"  icon="edit"></el-button>   
    </el-form>
  
    <div class="content">
      <el-table  :data="tableData"  style="width: 100%" >
        <el-table-column  label=""  width="100">
        <template scope="scope">
          {{scope.$index}}
        </template>
        </el-table-column>
        <el-table-column  prop="time" label=""  width="100"></el-table-column>
        <el-table-column  prop="name" label=""  width="100"></el-table-column>
        <el-table-column  prop="phone" label=""  width="100"></el-table-column>
        <el-table-column  prop="source" label=""  width="130"></el-table-column>
        <el-table-column  prop="indate" label=""  width="100"></el-table-column>
        <el-table-column  prop="usestate" label=""  width="100"></el-table-column>
       
        <el-table-column label="">
              <template scope="scope">
                <el-button size="small"  @click="handleEdit(scope.$index, scope.row)"> </el-button>
                <el-button size="small" type="danger"  @click="handleDelete(scope.$index, scope.row)"></el-button> 
              </template>
            </el-table-column>
          </el-table>
      </el-table>    
    </div>
  <!--  -->
    <el-dialog title="" v-model="dialogFormVisible" size="tiny" >
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="form.time"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.source"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.indate"></el-input>
        </el-form-item>
        <el-form-item label="">
        <el-select v-model="form.usestate" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
        </el-form-item>
        <el-form-item class="center">
          <el-button type="primary" @click="handleSave" :loading="editLoading"></el-button>
          <el-button @click="dialogFormVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>  
  <!--  -->
    <el-dialog title="" v-model="dialogFormVisibleadd" size="tiny">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="form.time"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.source"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.indate"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.usestate" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
        </el-form-item>
        <el-form-item class="center">
          <el-button type="primary" @click="handleSaveadd" :loading="editLoading"></el-button>
          <el-button @click="dialogFormVisibleadd = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>  
  <!--  -->
    <div class="block">
       <el-pagination
         @size-change="handleSizeChange"
         @current-change="handleCurrentChange"
         :current-page="currentPage1"
         :page-sizes="[10, 20, 30, 40]"
         :page-size="100"
         layout="total, sizes, prev, pager, next, jumper"
         :total="tableData.length">
       </el-pagination>
     </div>
  </div>
</template>
<script >

export default {
  name: 'appointreport',
  data () {
    return {

      dialogFormVisible:false,
      dialogFormVisibleadd:false,
      editLoading:false,//
      currentPage1: 5,
      activeIndex: '1',
      formInline: {
         time1: '',
         name1: '',
         state:'',
      },
       form: {
          time: '',
          name: '',
          phone:'',
          source:'',
          indate:'',
          usestate:'',
        },
      tableData: [{
                 
                 time: '2017-05-03',
                 name: '',
                 phone:'13800138000',
                 source:'',
                 indate:'20170503',
                 usestate:'',
               }, 
               {
                 
                 time: '2017-05-03',
                 name: '',
                 phone:'13800138000',
                 source:'',
                 indate:'20170503',
                 usestate:'',
               },
               {
                 time: '2017-05-03',
                 name: '',
                 phone:'13800138000',
                 source:'',
                 indate:'20170503',
                 usestate:'',
               }, 
               {
               
                 time: '2017-05-03',
                 name: '',
                 phone:'13800138000',
                 source:'',
                 indate:'20170503',
                 usestate:'',
               }]
    }
  },
  methods: {
     onSubmit() {
       this.$message('');
     },

     handleEdit (index, row) {
       this.dialogFormVisible = true;
       this.form = Object.assign({}, row);
       this.table_index = index;
     },

     handleSave () {
       this.$confirm('', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         cancelButtonClass: 'cancel'
       }).then(() => {
        this.editLoading = true;//
        this.tableData[this.table_index] = this.form;
        this.tableData.splice(this.table_index, 1, this.form);
        this.$message({
           message: "",
           type: 'success'
         });
         this.editLoading = false;
         this.dialogFormVisible = false;
       })
     },

     handleSaveadd () {

       this.$confirm('', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         cancelButtonClass: 'cancel'
       }).then(() => {
        //this.editLoading = true;//
        let vm=this;
        vm.tableData.push(vm.form);
        this.$message({
           message: "",
           type: 'success'
        });
        this.dialogFormVisibleadd = false;
       });
     },

     handleDelete (index, row) {
       this.tableData.splice(index, 1);
       this.$message({
         message: "",
         type: 'success'
       });
     },

     handleCreate(){    
      this.initform();// //
      this.dialogFormVisibleadd = true; 
     },

     //
     initform(){
      this.form={
          time: '',
          name: '',
          phone:'',
          source:'',
          indate:'',
          usestate:'',
        }
     },

      handleSizeChange(val) {
      console.log(` ${val} `);
      },

      handleCurrentChange(val) {
      console.log(`: ${val}`);
      },
      handleSelect(key, keyPath) {
      console.log(key, keyPath);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.appointreport{
  padding: 20px;
}

.appointreport .content{
 /* width: 95%;*/
  height: 450px;
  background: white;
 /* margin: 0 auto;*/
}
.appointreport .block{
  text-align: center;
}
.appointreport .center{
  text-align: center;
}
</style>



