<template>
	<div class="top">
		<!-- <el-row> -->
		<div>
			<!-- <el-button class="barsBtn">
				<i class="fa fa-bars" aria-hidden="true"></i>
			</el-button> -->
			<span class="font-time">&nbsp;{{ time.month }}&nbsp;{{ time.day }}&nbsp;&nbsp;&nbsp;{{ todos[time.Day] }}&nbsp;&nbsp;{{ time.hours }}:{{ time.minutes }}</span>
			<ul class="menu2">
				<li>
					<el-dropdown class="dropdown">
					    <span class="el-dropdown-link dropdown-link">
					   	 	<i class="Img">
					   	 		<img src="../../assets/logo.png">
					   	 	</i>
					   	 	<span>{{per.name}}</span>
					    </span>
					    <el-dropdown-menu slot="dropdown">
						    <el-dropdown-item>
						    	<router-link to='/home/personal' style="text-decoration:none;color:#666;"></router-link>
						    </el-dropdown-item>
						    <el-dropdown-item></el-dropdown-item> 
					  	</el-dropdown-menu>
					</el-dropdown>
				</li>
				<li>
					<i class="fa fa-envelope-o" aria-hidden="true"></i>
				</li>
				<li>
					<i class="fa fa-question-circle" aria-hidden="true"></i>
				</li>
				<li class="exit">
					<router-link to="/">
						<i class="fa fa-sign-in" aria-hidden="true"></i>
					</router-link>
				</li>
			</ul>
			</div>
		<!-- </el-row> -->
	</div>
</template>
<style type="text/css" scoped>
	.top{
		line-height: 50px;
		background: #fff;
	}
	/**/
	/*.top .barsBtn{
		border: 0;
		border-radius: 0;
		background: #f1f1f1;
		width: 50px;
		height: 50px;
	}*/
	
	/**/
	.top .font-time{
		margin-left: 15px;
		float: left;
	}
	.top .font-time,.top .dropdown,.top .menu2 li{
		color: #666;
		font-size: 14px;
	}
	.top .menu2{
		text-align: center;		
		float: right;
		margin-right:60px; 
	}
	.top .menu2 li{
		display: inline-block;
		list-style: none;
		text-align: center;
		margin-left: 30px;
	}
	.top .menu2 .exit a{
		text-decoration: none;
		color: #666;
		font-size: 14px; 
	}
	.top .menu2 li i{
		margin-right: 10px;
	}
	.top img{
		width: 30px;
		height: 30px;
		vertical-align: middle;   /**/
	}
	.top .dropdown-link{
		cursor: pointer;
	}
</style>
<script>
	import store from '../../store';
  	export default {
  		data() {
	    	return {
	    		per:store.state.per,
	    		time:{
	    			month:'',
	    			day:'',
	    			Day:'',
	    			hours:'',
	    			minutes:''
	    		},
	    		todos:["","","","","","",""]
	    	}
   		},
   		mounted:function(){
   			this.setTime();
   		},

   		methods:{
   			// 
   			setTime:function(){
   				let that = this;  
   				console.log("");
   				setInterval(function(){
   					var date=new Date(); //
   					that.time.month=date.getMonth()+1;   //this
	    			that.time.day=date.getDate();
	    			that.time.Day=date.getDay();
	    			that.time.hours=date.getHours();
	    			if(date.getMinutes<10){  
	    				that.time.minutes='0'+date.getMinutes();
	    			}
	    			else{
	    				that.time.minutes=date.getMinutes();
	    			}
	    			
   				},1000);
   			}
   		}
  	}
  	// var cla=document.getElementsByClassName("barsBtn");
  	// cla.onclick=function(){
  	// 	this.toggle("aa")
  	// }
</script>


